@import "../defaults";   // Global defaults.

// hpv24 Edge to Edge layout for content.
// -----------------------------------------------------------------------------

.bg-edge2edge {
  position: relative;
  right: 50%;
  left: 50%;
  flex: 0 0 100vw;
  width: 100vw;
  max-width: none;
  margin-right: -50vw !important;
  margin-left: -50vw !important;

  &::before {
    display: table;
    content: " ";
  }

  &::after {
    clear: both;
  }
}

body {
  overflow-x: hidden;
}
